import React, { useRef } from 'react';
import { motion } from 'framer-motion';
import { useTheme } from '../contexts/ThemeContext';
import videoSrc from '../assets/video4.webm'; // Import the video

function Services() {
  const { darkMode } = useTheme();
  const videoRef = useRef(null);

  return (
    <div className={`min-h-screen ${darkMode ? 'bg-gray-900 text-white' : 'bg-white text-gray-900'}`}>
      <div className="container mx-auto px-4 py-8 pt-20"> {/* Added pt-20 for top padding */}
        <h1 className="text-4xl font-bold mb-8 text-center">Nuestros Servicios Legales</h1>

        <section className="grid grid-cols-1 md:grid-cols-2 gap-8">
          {[
            {
              title: "Derecho Corporativo y Comercial",
              items: [
                "Constitución y estructuración de empresas",
                "Fusiones y adquisiciones",
                "Contratos comerciales y negociaciones",
                "Gobierno corporativo y cumplimiento normativo",
                "Asesoría en comercio internacional"
              ]
            },
            {
              title: "Propiedad Intelectual y Tecnología",
              items: [
                "Registro y protección de marcas y patentes",
                "Litigios de propiedad intelectual",
                "Contratos de licencia y transferencia de tecnología",
                "Protección de datos y privacidad",
                "Asesoría legal en e-commerce y startups tecnológicas"
              ]
            },
            {
              title: "Derecho Laboral",
              items: [
                "Contratos de trabajo y políticas laborales",
                "Negociación colectiva y relaciones sindicales",
                "Litigios laborales y resolución de conflictos",
                "Asesoría en seguridad social y beneficios",
                "Cumplimiento de normativas laborales"
              ]
            },
            {
              title: "Derecho Ambiental y Sostenibilidad",
              items: [
                "Cumplimiento de regulaciones ambientales",
                "Litigios y mediación en conflictos ambientales",
                "Asesoría en proyectos de energía renovable",
                "Evaluación de impacto ambiental",
                "Estrategias de sostenibilidad corporativa"
              ]
            }
          ].map((service, index) => (
            <motion.article 
              key={index} 
              className={`${darkMode ? 'bg-gray-800' : 'bg-white'} shadow-lg rounded-lg p-6`}
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.5, delay: index * 0.1 }}
            >
              <h2 className={`text-2xl font-semibold mb-4 ${darkMode ? 'text-white' : 'text-gray-900'}`}>{service.title}</h2>
              <ul className="list-disc list-inside space-y-2">
                {service.items.map((item, itemIndex) => (
                  <li key={itemIndex} className={darkMode ? 'text-gray-300' : 'text-gray-700'}>{item}</li>
                ))}
              </ul>
            </motion.article>
          ))}
        </section>

        {/* New section with video background */}
        <section className="relative py-24 mt-16 overflow-hidden rounded-xl">
          <div className="absolute inset-0 w-full h-full">
            <video
              ref={videoRef}
              autoPlay
              loop
              muted
              playsInline
              className="w-full h-full object-cover"
            >
              <source src={videoSrc} type="video/webm" />
              Your browser does not support the video tag.
            </video>
            <div className="absolute inset-0 bg-black opacity-60"></div>
          </div>

          <div className="relative z-10 container mx-auto px-4">
            <motion.div
              className="max-w-3xl mx-auto text-center text-white"
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.5 }}
            >
              <h2 className="text-3xl font-bold mb-6">Innovación y Excelencia en Servicios Legales</h2>
              <p className="text-xl mb-8">
                En nuestro bufete, combinamos años de experiencia con un enfoque innovador para ofrecer soluciones legales de vanguardia. Nos mantenemos a la par de las últimas tendencias y tecnologías para brindar un servicio excepcional a nuestros clientes.
              </p>
              <p className="text-xl mb-8">
                Nuestro compromiso con la excelencia y la atención personalizada nos permite abordar los desafíos legales más complejos con confianza y eficacia.
              </p>
              <motion.a
                href="/contacto"
                className="inline-block bg-white text-blue-600 px-8 py-3 rounded-full font-semibold text-lg transition-colors duration-300 hover:bg-blue-100"
                whileHover={{ scale: 1.05 }}
                whileTap={{ scale: 0.95 }}
              >
                Solicite una Consulta
              </motion.a>
            </motion.div>
          </div>
        </section>

        <div className="mt-12 text-center">
          <p className={`text-xl mb-4 ${darkMode ? 'text-gray-300' : 'text-gray-700'}`}>¿Necesita asesoría legal en un área específica?</p>
          <a 
            href="/contacto" 
            className={`${darkMode ? 'bg-blue-600 hover:bg-blue-700' : 'bg-blue-600 hover:bg-blue-700'} text-white px-6 py-2 rounded transition duration-300`}
          >
            Contáctenos para una Consulta Personalizada
          </a>
        </div>
      </div>
    </div>
  );
}

export default Services;